interface ProjectCardProps {
  projectInfo: {
    title: string;
    description: string;
    tags?: string[];
  };
}

const ProjectCard = ({ projectInfo }: ProjectCardProps) => {
  return (
    <div className="w-[320px] h-[360px] bg-[#323232] py-5 flex flex-col">
      <h3>{projectInfo.title}</h3>
      <p>{projectInfo.description}</p>
      <div>
        {projectInfo.tags.map((el, index) => (
          <ProjectTag key={index} />
        ))}
      </div>
    </div>
  );
};

export default ProjectCard;
